@charset "UTF-8";

@import url(../../node_modules/normalize.css/normalize.css);

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100%;
}

img,
embed,
object,
video {
  max-width: 100%;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  display: flex;
  width: 100%;
  max-width: 1240px;
  height: 100%;
  margin: 0 auto;
}

.section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.title {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #1f2122;
  font-size: 3.75rem;
  margin-bottom: 20px;
  line-height: 1;
}

.title_white {
  color: #ffffff;
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Ubuntu-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "BebasNeue";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/BebasNeue-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "FA";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/fa-solid-900.woff2") format("woff2");
}

.calc {
  display: flex;
  background-color: rgba(0, 0, 0, 0.4);
  width: 529px;
}

.header {
  display: flex;
  background-color: #ffffff;
  padding: 1rem;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero {
  background: url(../img/bg-hero.jpg) center/cover no-repeat;
}

.hero__wrap {
  position: relative;
  height: 100vh;
}

.hero__wrap::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0.85) 10%, rgba(0, 0, 0, 0) 100%);
}

.hero__content {
  display: flex;
  align-items: center;
  height: 100%;
}

.hero__left {
  flex-basis: 50%;
}

.hero__right {
  flex-basis: 50%;
}

.hero__title {
  display: flex;
  border-left: 10px solid #711f1f;
  padding-left: 0.625rem;
}

.logo {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #1f2122;
  font-size: 2.25rem;
}

.logo span {
  color: #711f1f;
}

.menu {
  display: flex;
}

.menu__list {
  display: flex;
}

.menu__item {
  margin-right: 1.5625rem;
}

.menu__link {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #1f2122;
  font-size: 1.125rem;
}

.phone {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.phone__number {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #1f2122;
  font-size: 2.25rem;
}

.phone__callback {
  color: #414141;
  font-weight: 300;
  text-decoration: underline;
}

.topline {
  background-color: #414141;
}

.topline__container {
  display: flex;
  justify-content: flex-end;
}

.topline__list {
  display: flex;
  padding: 1rem 0;
}

.topline__item {
  display: flex;
  margin-right: 1rem;
}

.topline__item:last-child {
  margin-right: 0;
}

.topline__link {
  color: #ffffff;
  font-size: 0.8125rem;
  font-weight: 300;
}

.topline__link::before {
  content: '';
  font-family: 'FA';
  margin-right: 0.625rem;
}

.topline__link_city::before {
  content: '';
}

.topline__link_address::before {
  content: '';
}

.topline__link_user::before {
  content: '';
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }
}